/****************************************
Basic HTML element Overrides 
****************************************/
html,
body {
  font-family: Arial, sans-serif;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  background-color: black;
}
H2 {
  margin: 0;
}
/****************************************
CSS Variables
****************************************/
:root {
  --Padding: 10px;
  --Margin: var(--Padding);
  font-size: 16px;
}
/****************************************
Main App Container
****************************************/
#App {
  box-sizing: border-box;
  min-height: 100vh;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}
#App * {
  box-sizing: border-box;
}
/****************************************
HUD & Overlay 
****************************************/
.HUD {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
}
.HUD section {
  height: 100%;
  width: 100%;
  display: grid;
  justify-items: center;
  align-items: center;
}
.HUD > section > * {
  border: 1px solid black;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 10px;
}
/****************************************
Intro & Loading
****************************************/
.Intro .loadingBar {
  border: 1px solid black;
  height: 15px;
  margin: 10px;
}
.Intro .loadingBar > div {
  background-color: black;
  height: 100%;
}
/****************************************
Main
****************************************/
.Main > section > * {
  align-self: end;
  justify-self: start;
  text-align: center;
}
.BtnHolder {
  all: unset;
  list-style-type: none;
}
.BtnHolder li {
  pointer-events: auto;
  border: 1px solid black;
  padding: 10px;
  margin: 5px;
  font-size: 1rem;
  color: white;
  background: rgba(0, 0, 0, 0.5);
  cursor: pointer;
}
.BtnHolder li:hover {
  background: rgba(0, 0, 0, 0.75);
}
